import { useState, useEffect, useCallback, useMemo } from 'react';
import { supabase } from '@/integrations/supabase/client';
import { Product, ProductFormData, mapDbProductToProduct, mapProductToDbProduct, ProductFilters } from '@/types';
import { useBusinessSettings } from './useBusinessSettings';
import { useStockHistory } from './useStockHistory';
import { useProductFilters } from './useProductFilters';
import { useBusiness } from '@/contexts/BusinessContext';
import { useQuery, useQueryClient } from '@tanstack/react-query';
import { clearInventoryCaches } from '@/utils/inventoryCacheUtils';

// Import our new Server Actions
import { getProductsAction, createProductAction, updateProductAction, deleteProductAction, updateProductsBulkAction } from '@/app/actions/products';

export const useProducts = (userId: string | undefined, initialPageSize: number = 50) => {
  const [products, setProducts] = useState<Product[]>([]);
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(initialPageSize);
  const [totalCount, setTotalCount] = useState(0);
  const [isTyping, setIsTyping] = useState(false);
  const { settings } = useBusinessSettings();
  const { createStockHistoryEntry } = useStockHistory(userId);
  const { currentBusiness } = useBusiness();
  const queryClient = useQueryClient();

  const { filters, setFilters, filteredProducts } = useProductFilters(products);
  const [typingTimer, setTypingTimer] = useState<NodeJS.Timeout | null>(null);

  const setFiltersWithTypingState = useCallback((newFilters: ProductFilters) => {
    if (newFilters.search !== filters.search) {
      setIsTyping(true);
      if (typingTimer) clearTimeout(typingTimer);
      const timer = setTimeout(() => {
        setIsTyping(false);
      }, 600);
      setTypingTimer(timer);
    }
    setFilters(newFilters);
  }, [filters.search, typingTimer, setFilters]);

  // Use Server Action instead of Supabase
  const loadProducts = useCallback(async (): Promise<{ products: Product[], count: number }> => {
    if (!userId || !currentBusiness) {
      return { products: [], count: 0 };
    }

    try {
      // Server Action call
      const result = await getProductsAction({
        userId,
        businessId: currentBusiness.id,
        page,
        pageSize,
        search: filters.search,
        category: filters.category,
        stockStatus: filters.stockStatus,
      });
      return result;
    } catch (error) {
      console.error('Error loading products from server action:', error);
      return { products: [], count: 0 };
    }
  }, [userId, currentBusiness?.id, page, pageSize, filters.search, filters.category, filters.stockStatus]);

  const baseQueryKey = useMemo(() => ['products', userId, currentBusiness?.id], [userId, currentBusiness?.id]);
  const queryKey = useMemo(() => [...baseQueryKey, page, pageSize, filters.search, filters.category, filters.stockStatus], [baseQueryKey, page, pageSize, filters.search, filters.category, filters.stockStatus]);

  const { data: queriedData, isLoading: isQueryLoading, isFetching, refetch } = useQuery({
    queryKey,
    queryFn: loadProducts,
    enabled: !!userId && !!currentBusiness?.id,
    staleTime: 30_000,
    gcTime: 30 * 60_000,
    refetchOnWindowFocus: true,
    refetchOnReconnect: true,
  });

  useEffect(() => {
    if (queriedData) {
      setProducts(queriedData.products);
      setTotalCount(queriedData.count);
    }
  }, [queriedData]);

  const isLoading = (isQueryLoading && !queriedData) && !isTyping;

  // Supabase Storage remains untouched since Prisma doesn't do file storage
  const uploadProductImage = async (imageFile: File): Promise<string | null> => {
    try {
      if (!userId) return null;
      const fileExt = imageFile.name.split('.').pop();
      const fileName = `${Date.now()}.${fileExt}`;
      const filePath = `${userId}/${fileName}`;

      const { error: uploadError } = await supabase.storage
        .from('product-images')
        .upload(filePath, imageFile, { upsert: true });

      if (uploadError) {
        console.error('Error uploading image:', uploadError);
        return null;
      }

      const { data: { publicUrl } } = supabase.storage
        .from('product-images')
        .getPublicUrl(filePath);

      return publicUrl;
    } catch (error) {
      console.error('Error in uploadProductImage:', error);
      return null;
    }
  };

  const createProduct = async (productData: ProductFormData): Promise<Product | null> => {
    try {
      if (!userId || !currentBusiness) return null;

      // Assuming createProductAction exists and is fully implemented:
      const newProduct = await createProductAction({
        ...productData,
        userId,
        businessId: currentBusiness.id
      });

      if (!newProduct) return null;

      if (newProduct.quantity > 0) {
        await createStockHistoryEntry(
          newProduct.id,
          0,
          newProduct.quantity,
          'Initial stock',
          undefined,
          productData.createdAt,
          undefined,
          newProduct.name
        );
      }

      setProducts(prev => [newProduct, ...prev]);
      setTotalCount(c => c + 1);

      queryClient.setQueryData(queryKey, (oldData: any) => {
        if (!oldData) return { products: [newProduct], count: 1 };
        return {
          products: [newProduct, ...oldData.products],
          count: (oldData.count || 0) + 1
        };
      });

      clearInventoryCaches(queryClient);

      return newProduct;
    } catch (error) {
      console.error('Error creating product:', error);
      return null;
    }
  };

  const updateProduct = async (
    id: string,
    updates: Partial<Product>,
    imageFile?: File | null,
    isFromSale = false,
    customChangeReason?: string,
    adjustmentDate?: Date,
    referenceId?: string,
    receiptNumber?: string
  ): Promise<boolean> => {
    try {
      if (!userId) return false;

      let currentProduct = products.find(p => p.id === id);
      if (!currentProduct) return false;

      let imageUrl = updates.imageUrl;
      if (imageFile) {
        imageUrl = await uploadProductImage(imageFile);
      }

      const updatedData = { ...updates, imageUrl, barcode: updates.barcode !== undefined ? updates.barcode : currentProduct.barcode };

      // We will create the update updateProductAction next
      await updateProductAction(id, { ...updatedData, userId, businessId: currentBusiness?.id });

      setProducts(prev => prev.map(p => p.id === id ? { ...p, ...updatedData } : p));
      queryClient.setQueryData(queryKey, (oldData: any) => {
        if (!oldData) return oldData;
        return {
          ...oldData,
          products: oldData.products.map((p: Product) =>
            p.id === id ? { ...p, ...updatedData } : p
          )
        };
      });

      if (updates.quantity !== undefined && updates.quantity !== currentProduct.quantity && customChangeReason !== 'skip-history') {
        let changeReason = customChangeReason || (isFromSale ? 'Sale' : (updates.quantity > currentProduct.quantity ? 'Manual stock addition' : 'Manual stock reduction'));
        await createStockHistoryEntry(
          id,
          currentProduct.quantity,
          updates.quantity,
          changeReason,
          referenceId,
          adjustmentDate,
          receiptNumber,
          currentProduct.name
        );
      }

      queryClient.invalidateQueries({ queryKey: baseQueryKey });
      clearInventoryCaches(queryClient);
      return true;
    } catch (error) {
      console.error('Error updating product:', error);
      return false;
    }
  };

  const deleteProduct = async (id: string): Promise<boolean> => {
    try {
      if (!userId) return false;

      await deleteProductAction(id);

      setProducts(prev => prev.filter(p => p.id !== id));
      queryClient.setQueryData(queryKey, (old: any) => {
        if (!old) return old;
        const { products: oldProducts, count } = old;
        const newProducts = (oldProducts as Product[]).filter(p => p.id !== id);
        return { products: newProducts, count: Math.max(0, (count || 0) - 1) };
      });

      queryClient.invalidateQueries({ queryKey: baseQueryKey });
      clearInventoryCaches(queryClient);
      return true;
    } catch (error) {
      console.error('Error deleting product:', error);
      return false;
    }
  };

  useEffect(() => {
    // In Prisma, we either use WebSockets, polling, or React Query's built-in focus/refetch handlers
    // Disabled Supabase Realtime Channels since we moved off the raw Supabase client for data fetches
  }, [userId, currentBusiness?.id, baseQueryKey]);

  const updateProductsBulk = async (
    updates: Array<{ id: string; updated: Partial<Product>; imageFile?: File | null }>,
    userIdForHistory?: string,
    changeReason?: string,
    referenceId?: string,
    adjustmentDate?: Date,
    receiptNumber?: string
  ): Promise<boolean> => {
    try {
      if (!userId || !currentBusiness) return false;

      // Basic implementation without handling independent image uploads for bulk right now
      const success = await updateProductsBulkAction(
        updates.map(u => ({ id: u.id, updated: u.updated })),
        currentBusiness.id
      );

      if (success) {
        // Optimistically update UI
        setProducts(prev => {
          const updatedMap = new Map(updates.map(u => [u.id, u.updated]));
          return prev.map(p => {
            const updatedItem = updatedMap.get(p.id);
            return updatedItem ? { ...p, ...updatedItem } as Product : p;
          });
        });

        // Add history for quantity changes
        for (const update of updates) {
          const currentProduct = products.find(p => p.id === update.id);
          if (currentProduct && update.updated.quantity !== undefined && update.updated.quantity !== currentProduct.quantity) {
            await createStockHistoryEntry(
              update.id,
              currentProduct.quantity,
              update.updated.quantity,
              changeReason || 'Bulk update',
              referenceId,
              adjustmentDate,
              receiptNumber,
              currentProduct.name
            );
          }
        }

        queryClient.invalidateQueries({ queryKey: baseQueryKey });
        clearInventoryCaches(queryClient);
      }

      return success;
    } catch (error) {
      console.error('Error in bulk update:', error);
      return false;
    }
  };

  return {
    products,
    isLoading,
    loadProducts,
    page,
    setPage,
    pageSize,
    setPageSize,
    totalCount,
    createProduct,
    updateProduct,
    updateProductsBulk,
    deleteProduct,
    uploadProductImage,
    refetch,
    isFetching,
    filters,
    setFilters: setFiltersWithTypingState,
    filteredProducts
  };
};
